.openList {
    width: 100%;
    background-color: #FFF;
    padding: 42px;
}

.openListHeader {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 48px;
}

.backButton {
    width: 36px;
    height: 36px;
    background-image: url(./back.svg);
    background-size: 12px;
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 12px;
    cursor: pointer;
}

.searchBar {
    display: flex;
    width: 100%;
    height: 36px;
}

.openList h1 {
    margin: 0;
}

.select {
    display: flex;
}

.searchBarSelectContainer {
    width: 100%;
    display: flex;
}

.btn {
    transition: background-color 0.2s ease-in-out;
    white-space: nowrap;
    padding: 13px;
    height: 36px;
    border-radius: 6px;
    width: 36px;
    text-align: left;
    background-color: rgba(0, 0, 0, 0);
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 12px;
}

.select .btn:nth-child(1) {
    background-image: url(./button.svg);
}

.select .btn:nth-child(2) {
    background-image: url(./cards.svg);
}

a.active {
    background-color: #E4E7EC
}

.add {
    transition: all .5s ease-in-out;
    position: fixed;
    bottom: 34px;
    left: 0;
    margin-left: calc(var(--sideBar-closed-width) + 36px);
    background: #667085;
    border-radius: 50%;
    padding: 16px;
    width: 48px;
    height: 48px;
    cursor: pointer;
    background-image: url(./+.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 16px;
}