.sideBarButton {
    transition: background-color 0.2s ease-in-out;
    white-space: nowrap;
    padding: 13px;
    height: 40px;
    border-radius: 6px;
    width: 100%;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    text-align: left;
    background-color: rgba(0, 0, 0, 0);
    margin-bottom: 10px;
}

.link {
    transition: visibility 0.1s, opacity 0.1s;
}
a.sideBarButton {
    text-decoration: none;
    color: #101828;
}

.sideBarButton .img {
    content: '';
    background-image: url(./star.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 14px;
    height: 14px;
    display: inline-block;
    margin-right: 7px;
}


.sideBarButton:hover {
    background-color: #E4E7EC;
}

.sideBarButton.active {
    background-color: #E4E7EC !important;
}



.close-anim-enter .link{
    opacity: 1;
}

.close-anim-enter-active .link{
    opacity: 0;
    transition: opacity 200ms;
}

.close-anim-exit .link, .sideBarButton.close .link{
    opacity: 0;
}

.close-anim-enter-done .link{
    opacity: 0;
}

.close-anim-exit-active .link{
    opacity: 1;
    transition: opacity 200ms;
}

.close-anim-exit-done .link{
    opacity: 1;
}