.lists {
    width: 100%;
    background-color: #FFF;
    padding: 42px;
}

.searchBar {
    display: flex;
    width: 100%;
    height: 36px;
}

.searchBarContainer {
    width: 100%;
}

.buttonsContainer {
    height: 100%;
    display: flex;
    justify-content: space-between;
}

.button {
    display: flex;
    align-items: center;
    width: 108px;
    height: 36px;
    background: #667085;
    border-radius: 6px;
    font-family: 'Source Sans Pro';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 18px;
    margin-left: 18px;
    color: #FFF;
}

.button::before {
    content: '';
    background-image: url(./import.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 14px;
    height: 14px;
    display: inline-block;
    margin-right: 7px;
}

.button:nth-child(2)::before {
    background-image: url(./create.svg);
}

.addTag {
    background-image: url(./addTag.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 20px;
    height: 20px;
}

.tagsFilterSortContainer {
    display: flex;
    margin-top: 36px;
}

.tagContainer {
    display: flex;
    align-items: center;
}
.tagContainer .tag {
    cursor: pointer;
}

.sortFilterContainer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
}

.sortFilter {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #101828;
    background-color: unset;
    user-select: none;

}
.listContainer{
    margin-top: 38px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}