.view{
    transition: margin 0.5s ease-in-out, width 0.5s ease-in-out;
    width: calc(100% - var(--sidBar-width));
    background-color: #FFF;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    overflow-y: auto;
    margin-left: var(--sidBar-width);
}
.viewClose{
    width: calc(100% - var( --sideBar-closed-width));
    margin-left: var( --sideBar-closed-width);
}