.table {
    margin-top: 36px;
    display: flex;
}
.column{
    width: 100%;
}
.row {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    box-shadow: inset 0px -1px 0px #E1E3EB;
    padding: 20px;
    display: flex;
    align-items: center;
}
.center{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 36px;
}