.tag {
    display: inline-block;
    padding: 3px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    color: #344054;
    border: 1px solid #D0D5DD;
    border-radius: 6px;
    min-width: 40px;
    margin-right: 24px;
    user-select: none;
}

.tag.selected {
    color: #101828;
    border: none;
    font-size: 16px;
    line-height: 19px;
}