.list {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 82px;
    background: #FFFFFF;
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
    padding: 17px;
    margin-bottom: 18px;
}

.button {
    background-image: url(./button.svg);
    background-color: #66708529;
    border-radius: 4px;
    width: 48px;
    height: 48px;
    padding: 0;
    background-size: 40%;
    background-position: center;
    background-repeat: no-repeat;
}

.listBody {
    display: flex;
    align-items: center;
    height: 100%;
}

.listHeader {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #101828;
    margin-left: 18px;
}

.listTags {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-left: 18px;
}

.data {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.dataItem {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #101828;
}

.membersCount {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #9A9A9A;
}
.data > *{
    margin-left: 24px;
}
.share {
    width: 15px;
    height: 15px;
    background-image: url(./share.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
}