.btn {
    display: flex;
    align-items: center;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    height: 48px;
    user-select: none;
    cursor: pointer;
    min-width: 40px;
}

.btn::after {
    transition: transform 0.2s ease-in-out;
    display: inline-block;
    content: '';
    background-image: url(./arrow.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 9px;
    height: 6px;
    margin-left: 9px;
}
.btn.sort::after{
    transform: rotateZ(180deg);
}
.sort{}