.logoContainer {
    transition: width 0.5s ease-in-out;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 98px;
    height: 53px;
    border: 1px solid #344C54;
    border-radius: 6px;
    padding: 12px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: #475467;
    overflow: hidden;
}
.close{
    width: 100%;
    justify-content: flex-start;
}