.sideBarOpen {
    transition: width 0.5s ease-in-out;
    background-color: #F2F4F7;
    min-height: 100vh;
    width: var(--sidBar-width);
    flex-shrink: 0;
    position: absolute;
    padding:36px;
}
.sideBarButtonContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    margin-top: 36px;
}
.sideBarClose {
    transition: width 0.5s ease-in-out;
    background-color: #F2F4F7;
    min-height: 100vh;
    width: var( --sideBar-closed-width);
    flex-shrink: 0;
    position: absolute;
    padding: 12px;
    overflow-x: hidden;
}
